.App {
  text-align: center;
}

/* New stuff*/
a {
  text-decoration: none;
  color: black;
}
.details{
  height: 100vh;
}
a:nth-child(even) .room{
  background-color: #D9D9D9 ;
  margin:auto;
  height: 30px;
}
a:nth-child(odd) .room{
  background-color: #c1c1c1 ;
  margin:auto;
  height: 30px;
}
.room h1 {
  font-size: 5vw;
}
.roomTitle {
  margin:0;
  width: 100%;
}
.searchResults {
  position: absolute;
  left: 0; 
  right: 0; 
  margin: auto;
  width: 50%;
  margin-top: 5vh;
  border:2px solid black;
}

svg {
  margin-top: 0vh;
}

/*CSS för svg kartan på /map/ */
#Lager_2{
  margin-top: 20vh;
}

/*Text för kartan*/
#description{
  text-align: center;
}

#description h1{
  margin-top: 4vh;
}

#description h2{
  margin-top: 5vh;
  margin-bottom: 0vh;
}

#input{
  font-size: 4vw;
  width: 100%;
  height: 30px;
  padding: 0;
  border: 0;
}

#tillbakaKnapp{
  color:black;
  background-color: #00B3E1;
  height: 10vh;
  width: 17vw;
  position: absolute;
}

/*Våningsknapparna på /map/ */
#floorContainer{
  position: absolute;
  bottom: 0;
  width: 100%;
}

#floorContainer button{
  width: 100%;
}
